import React, { useState } from 'react';
import './styles.css';
import dayjs from 'dayjs';
import {
  Form,
  Input,
  Button,
  Select,
  DatePicker,
  InputNumber,
  message,
  Row,
  Card,
} from 'antd';
import { FormInstance } from 'antd/es/form';
import {
  birthDateRule,
  emailRule,
  experienceRule,
  maxLengthRule,
  minLengthRule,
  phoneNumberRule,
  requiredRule,
} from '../validationRules';
import { FormValues } from '../types';
import { INIT_VALUES, POSITIONS } from '../constants';
import { mapServerDataToFormValues } from '../utils';

const { TextArea } = Input;
const { Option } = Select;

const AdForm: React.FC = () => {
  const [form] = Form.useForm<FormInstance<FormValues>>();
  const [editing, setEditing] = useState<boolean>(false);
  const [lastSavedValues, setLastSavedValues] = useState(
    mapServerDataToFormValues(INIT_VALUES)
  );

  const toggleEdit = () => {
    if (editing) {
      form.resetFields();
    }
    setEditing(!editing);
  };

  const onSave = () => {
    form
      .validateFields()
      .then((values: any) => {
        const transformedValues = {
          ...values,
          birthDate: values.birthDate.toDate(),
        };
        setLastSavedValues(transformedValues);
        setEditing(false);
        message.success(' !');
      })
      .catch((error) => {
        console.log(' :', error);
      });
  };

  const onCancel = () => {
    form.resetFields();
    setEditing(false);
  };

  return (
    <Card className='form-wrap'>
      <Form
        form={form}
        initialValues={{
          ...lastSavedValues,
          birthDate: dayjs(lastSavedValues.birthDate),
        }}
        layout='vertical'
        disabled={!editing}
        requiredMark={false}
        className='form'
      >
        <Form.Item>
          <Form.Item
            label=''
            name='fullName'
            rules={[requiredRule(',  ')]}
          >
            <Input />
          </Form.Item>
        </Form.Item>

        <Form.Item className='form-row'>
          <Form.Item
            label=' '
            name='birthDate'
            rules={[birthDateRule]}
            className='form-field'
          >
            <DatePicker
              format='DD.MM.YYYY'
              onChange={() => form.validateFields(['experience'])}
            />
          </Form.Item>
          <Form.Item
            label=' ()'
            name='experience'
            rules={[experienceRule(form.getFieldValue)]}
            className='form-field'
          >
            <InputNumber min={0} />
          </Form.Item>
        </Form.Item>

        <Form.Item>
          <Form.Item label='' name='position'>
            <Select>
              {POSITIONS.map((position) => (
                <Option key={position} value={position}>
                  {position}
                </Option>
              ))}
            </Select>
          </Form.Item>
        </Form.Item>

        <Form.Item className='form-row'>
          <Form.Item
            label=''
            name='username'
            rules={[
              requiredRule(',  '),
              minLengthRule(3, '    3 '),
              maxLengthRule(20, '    20 '),
            ]}
            className='form-field'
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=''
            name='password'
            rules={[
              minLengthRule(6, '    6 '),
              maxLengthRule(12, '    12 '),
            ]}
            className='form-field'
          >
            <Input.Password />
          </Form.Item>
        </Form.Item>

        <Form.Item className='form-row'>
          <Form.Item
            label='Email'
            name='email'
            rules={[requiredRule(',  email'), emailRule]}
            className='form-field'
          >
            <Input />
          </Form.Item>
          <Form.Item
            label=' '
            name='phoneNumber'
            className='form-field'
            rules={[phoneNumberRule()]}
          >
            <Input />
          </Form.Item>
        </Form.Item>

        <Form.Item
          label=''
          name='notes'
          rules={[
            maxLengthRule(
              400,
              '     400 '
            ),
          ]}
        >
          <TextArea rows={4} />
        </Form.Item>
      </Form>
      <Row className='button-wrap'>
        {!editing ? (
          <Button type='primary' onClick={toggleEdit}>
            
          </Button>
        ) : (
          <>
            <Button type='primary' onClick={onSave}>
              
            </Button>
            <Button onClick={onCancel}></Button>
          </>
        )}
      </Row>
    </Card>
  );
};

export default AdForm;
